import {
    Button,
    Space,
    FloatingPanel,
    Popup,
    ConfigProvider,
    ErrorBlock,
    setDefaultConfig,
    FloatingBubble,
} from "antd-mobile";
import {useContext, useState} from "react";
import BasePage from "./BasePage";

import Settings, {ColorContext, ColorType, DaynightContext, SettingsContext, ThemeContext} from './Settings'

export default function EnglishPage({onBack}) {
    const {settings, setSettings} = useContext(SettingsContext)

    return <BasePage
        naviTitle={'英语'}
        onBack={onBack}
        naviRight={undefined}
    >
        <div
            style={{
                flex: 1, width: '100%',
                backgroundColor: settings.color[1],
            }}>

        </div>
    </BasePage>
}
